.icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #ccc; */
}
.icon-container-check {
  position: absolute;
  left: 12px;
}
.icon-container-check--active {
  color: #4caf50;
}
.icon-container-delete {
  position: absolute;
  right: 0;
}
.icon-container-delete:hover {
  color: red;
}

.icon.svg {
  width: 24px;
  height: 24px;
}

.icon-container-delete:hover .icon-svg {
  fill: red;
}
.icon-container-check:hover .icon-svg {
  fill: green;
}
